.MovieForm{
    -ms-flex: 0 0 32%;
        flex: 0 0 32%;
    padding: 20px;
    font-size: 24px;
    background-color: #a6bfcc;
    -webkit-box-shadow: 0 0 3px 3px rgba(33,33,33,.7);
            box-shadow: 0 0 3px 3px rgba(33,33,33,.7);
    border-radius: 2px;
}

.MovieForm * {
    font-size: 24px;
}

.MovieForm__label{
    width: 100%;
    color: #000;
}

.MovieForm__input{
    width: 100%;
    margin-bottom: 10px;
}

.MovieForm__textarea{
    width: 100%;
    margin-bottom: 10px;
}

.MovieForm__submit{
    display: block;
    margin: auto;
}

.MovieForm__submit:hover{
    -webkit-transition: background-color 0.5s linear;
    -o-transition: background-color 0.5s linear;
    transition: background-color 0.5s linear;
    background-color: #00bcd4;
}

.MovieForm__genres{
    margin-bottom: 10px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    text-align: center;
    vertical-align: middle;
}

.MovieForm__genres label{
    margin-right: 10px;
    margin-bottom: 5px;
    display: -ms-flexbox;
    display: flex;
    cursor: pointer;
    font-size: 16px;
    -ms-flex-line-pack: center;
        align-content: center;
}

.MovieForm__genres input{
    margin-right: 4px;
    width: 20px;
    height: 20px;
}

